import { Component, h, Prop } from '@stencil/core';
import { article, ArticleData, Content } from "../../../global/data/content";

declare global { interface Window { dataLayer: any[] }}

@Component({
    tag: 'ks-page-article',
    styleUrl: 'article.css'
})
export class PageArticle{
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() articleData: string;

    componentWillLoad() {
        const articleDataElement = document.getElementById(this.articleData);
		const articleData = JSON.parse(articleDataElement.innerHTML) as ArticleData;

        Object.keys(articleData).map(key => {
			article.set(key as keyof ArticleData, articleData[key]);
		});
    }

    renderDescription(content: Content, type: string) {
        return content?.map(entry => {
            switch (entry.type) {
                case "ComponentContentText":
                    return <ks-description-text 
                        text={entry.text}
                        align={entry.align}
                        nomargin={entry.nomargin}
                    />

                case "ComponentContentImage":
                    return <ks-description-image 
                        image={entry.image}
                        width={entry.width} 
                    />

                case "ComponentContentTextAndImage":
                    return <ks-description-text-image 
                        text={entry.text}
                        image={entry.image}
                        width={entry.width} 
                        alignment={entry.alignment}
                        position={entry.position}
                    />

                case "ComponentContentProducts":
                    return <ks-description-products 
                        products={entry.products}
                        kind={entry.kind}
                    />

                case "ComponentContentHtml":
                    return <ks-description-html 
                        html={entry.html}
                    />

                case "ComponentContentLinkTiles":
                    return <ks-content-link-tiles content={entry} />
                    
                case "ComponentContentFeaturedAndText":
                    return <ks-content-featured-with-text content={entry} />

                case "ComponentContentAbout":
                    return <ks-content-about content={entry} />

                case "ComponentContentBreadcrumbs":
                    return <ks-content-breadcrumbs items={entry.items} />

                case "ComponentContentTags":
                    return <ks-content-tags items={entry.items} />

                case "ComponentContentSeparator":
                    return <ks-content-separator content={entry} />

                case "ComponentContentButton":
                    return <ks-content-button content={entry} />

                case "ComponentContentBannerAndText":
                    return <ks-content-banners content={entry.banners} />

                case "ComponentContentVisualCampaign":
                    return entry.kind == 'banners' ? <ks-content-banners content={entry.banners} home={type=="homepage"} /> :
                           entry.kind == 'thumbnails' ? <ks-content-thumbnails content={entry.thumbnails} /> :
                           entry.kind == 'featured' ? <ks-content-featured content={entry.featured} /> :
                           null;

                case "ComponentContentReviews":
                    return <ks-content-reviews content={entry} grey/>;

                case "ComponentContentRecipes":
                    return <ks-content-recipes content={entry}/>;

                case "ComponentContentArticles":
                    return <ks-content-articles content={entry}/>;

                default: return null;
            }
        });
    }

	render() {
        const fullWidth = article.get("fullWidth") ? "full-width" : "";
        const noPadding = article.get("noPadding") ? "no-padding" : "";

        const infoBanner = article.get('infoBanner');
        
		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            {infoBanner ?
                <ks-content-info-banner content={infoBanner} />
            : null}
            
            <div class={["article-container", fullWidth, noPadding].join(" ")}>
                {this.renderDescription(article.get('content'), article.get("type"))}
            </div>
        </ks-page-base>;
	}
}
