import { createStore } from "@stencil/store";
import { NavbarTheme, InfoBannerData, ReviewAverage } from "./common";
import { ProductCardData, NameLinkData } from "./listing";
import { RecipeCardData } from "./recipes";
import { ArticleCardData } from "./articles";

export const article = createStore({} as ArticleData);

export interface ArticleData {
    heading: string;
    type: string;
    fullWidth: boolean;
    noPadding: boolean;
    content: Content;
    infoBanner: InfoBannerData;
}

export type Content = (
    DescriptionText |
    DescriptionImage |
    DescriptionTextImage |
    DescriptionProducts |
    DescriptionHTML |
    BannerWithText |
    LinkTiles |
    FeaturedWithText |
    About |
    Breadcrumbs |
    Tags | 
    Separator |
    Button |
    VisualCampaign |
    Reviews |
    Recipes |
    Articles
)[];

export interface DescriptionText {
    type: "ComponentContentText";
    text: string;
    align: "left" | "center" | "right";
    nomargin: boolean;
}

export interface DescriptionImage {
    type: "ComponentContentImage";
    image: string;
    width: number;
}

export interface DescriptionTextImage {
    type: "ComponentContentTextAndImage";
    text: string;
    image: string;
    width: number;
    position: string;
    alignment: string;
}

export interface DescriptionProducts {
    type: "ComponentContentProducts";
    products: ProductCardData[];
    kind: "standard" | "simple";
}

export interface DescriptionHTML {
    type: "ComponentContentHtml";
    html: string;
}

export interface LinkTiles {
    type: "ComponentContentLinkTiles";
    tiles: LinkTilesItem[];
    gap: number;
}

export interface LinkTilesItem {
    name: string;
    image: string;
    webp: string;
    width: number;
    height: number;
    
    links: {
        name: "string";
        href: "string";
    }[];
}

export interface FeaturedWithText {
    type: "ComponentContentFeaturedAndText";
    featured: FeaturedWithTextItem[];
    gap: number;
}

export interface FeaturedWithTextItem {
    name: string;
    aboveText: string,
    mainText: string,
    link: string;
    
    fit: "contain" | "cover" | "fill" | "scale-down" | "none",
    
    verticalAlignment: "top" | "center" | "bottom",
    horizontalAlignment: "left" | "center" | "right",

    backgroundColor: string;
    textColor: string;

    image: string,
    webp: string,
    width: number,
    height: number
}

export interface About {
    type: "ComponentContentAbout";
    image: string;
    webp: string;
    width: number;
    height: number;

    heading: string;
    paragraph: string;
    items: AboutItems[];

    style: "full" | "expand";
}

export interface AboutItems {
    image: string;
    webp: string;
    width: number;
    height: number;

    heading: string;
    paragraph: string;
}


export interface Breadcrumbs {
    type: "ComponentContentBreadcrumbs";
    items: NameLinkData[];
}

export interface Tags {
    type: "ComponentContentTags";
    items: NameLinkData[];
}

export interface Separator {
    type: "ComponentContentSeparator";
    kind: "line" | "empty";
    spacing: "zero" | "small" | "medium" | "large";
}

export interface Button {
    type: "ComponentContentButton";
    name: string;
    link: string;
    nofollow: boolean;
    style: "default" | "border" | "primary" | "secondary" | "transparent";
    align: "left" | "center" | "right" | "full";
    size: "default" | "narrow" | "narrower" | "tall";
    round: boolean;
}

export interface VisualCampaign {
    type: "ComponentContentVisualCampaign";
    kind: 'banners' | 'thumbnails' | 'featured';
    banners?: Banner[];
    thumbnails?: Thumbnail[];
    featured?: FeaturedBanner[];
}

export interface Thumbnail {
    name: string;
    link: string;

    image: string,
    webp: string,
    width: number,
    height: number
}

export interface BannerWithText {
    type: "ComponentContentBannerAndText";
    banners: Banner[];
}

export interface Banner {
    name: string;
    link: string;

    aboveText: string,
    mainText: string,
    
    fit: "contain" | "cover" | "fill" | "scale-down" | "none",
    
    verticalAlignment: "top" | "center" | "bottom",
    horizontalAlignment: "left" | "center" | "right",

    backgroundColor: string;
    textColor: string;

    image: string,
    webp: string,
    width: number,
    height: number,

    theme: NavbarTheme;
}

export interface FeaturedBanner {
    name: string;
    link: string;

    image: string,
    webp: string,
    width: number,
    height: number
}

export interface Reviews extends ReviewAverage {
    type: "ComponentContentReviews";
}

export interface Recipes {
    type: "ComponentContentRecipes";
    recipes: RecipeCardData[];
}

export interface Articles {
    type: "ComponentContentArticles";
    articles: ArticleCardData[];
}