ks-page-article {
    display: block;
}

ks-page-article .article-container {
    box-sizing: border-box;
    max-width: 1200px;
    width: 100%;

    margin: 0 auto 0 auto;
    padding: 40px;

    background: #fff;
    color: #373737;
}

ks-page-article .article-container.full-width {
    max-width: 100%;
}

ks-page-article .article-container.no-padding {
    padding: 0px;
}

ks-page-article h1 {
    font-size: 26px;
    font-weight: 700;
}

ks-page-article h2 {
    font-size: 20px;
    font-weight: 700;
}

ks-page-article h3 {
    font-size: 18px;
    font-weight: 700;
}

ks-page-article hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
    text-align: inherit;
    
    border: 0;
    border-top: 1px solid #e5e5e5;
}

@media screen and (max-width: 640px) {
    ks-page-article .article-container {
        padding: 40px 20px;
    }
}